.App {
  width: auto;
  height: 100vh; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

header {
  background-color: black;
  width: 100%;
  z-index: 100;
  height: 80px;
  line-height: 80px;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0px; }
  header .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 50px;
    margin-top: -8px;
    text-align: right; }

h1 {
  font-size: 160%;
  display: inline-block;
  color: #FFFFFF; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@media only screen and (max-width: 500px) {
  header {
    height: 60px;
    line-height: 60px; }
  h1 {
    font-size: 100%; } }

footer {
  background-color: rgba(0, 0, 0, 0.8);
  bottom: 0px;
  position: absolute;
  width: 100%;
  height: 40px;
  line-height: 40px;
  z-index: 100;
  color: #FFFFFF; }
  footer .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 20px;
    text-align: right; }



